import './App.css';
import ActivityCalendar from "react-activity-calendar";
// import data from "./trash/test.json";
import { Tooltip } from 'react-tooltip'
// import "react-tooltip/dist/react-tooltip.css";
import React, { useEffect, useState } from 'react';

const explicitTheme = {
  light: ['#f0f0f0', '#e1b182', '#f18931', '#e94822', '#9f2816'],
  dark: ['#f0f0f0', '#e1b182', '#f18931', '#e94822', '#9f2816'],
};

const labeltest = {
  months: [
    '一月',
    '二月',
    '三月',
    '四月',
    '五月',
    '六月',
    '七月',
    '八月',
    '九月',
    '十月',
    '十一月',
    '十二月',
  ],
  weekdays: ['日', '一', '二', '三', '四', '五', '六'],
  totalCount: '🍅🍅喂了{{count}}次鱼食 in {{year}}',
  legend: {
    less: '饿',
    more: '撑',
  }
}

function App() {
  const [data, setData] = useState([]);
  const [dothings, setDothings] = useState([]);

  useEffect(() => {

    let ignore = data.length;

    if (ignore === 0) {
      fetch('./2024.json').then((response) => response.json())
        .then(result => {
          if (!ignore) {
            setData(result);
          }

          return () => {
            ignore = 1;
          };
        })
    }
    let ignore2 = data.length;
    if (ignore2 === 0) {
      fetch('./2024things.json').then((response) => response.json())
        .then(result => {
          if (!ignore) {
            setDothings(result);
          }

          return () => {
            ignore2 = 1;
          };
        })
    }



  }, [data,dothings])


  return (
    <div className="App">
      <header className="App-header">
        <div className='zhaopai'>
          <h1 className='neonText'>于适大饭店</h1>
          <h2 className='neonText'>yosh's world</h2>
          {/* <h2 className='neonText second2' >今日开张</h2> */}
        </div>

        <section>
          <h1 className='thetile'>今天吃🍅或🥫了吗？</h1>
        </section>

        <ActivityCalendar className="firsttset" data={data} theme={explicitTheme}
          eventHandlers={{
            onClick: (event) => (activity) => {
              for (let index = 0; index < dothings.length; index++) {
                const element = dothings[index];
                if (activity.date === element.date) {
                document.getElementById("things").innerHTML=element.things;
                }
              }

              
            }
          }}
          renderBlock={(block, activity) =>
            React.cloneElement(block, {
              'data-tooltip-id': 'react-tooltip',
              'data-tooltip-html': `${activity.date}吃饱程度${activity.level * 25}%🍚</br>`,
            })} showWeekdayLabels labels={labeltest} hideTotalCount={false} weekStart={1} />
          <div className='dothings'>
            <p id = "things"></p>
            </div>
          


      </header>
      <Tooltip id="react-tooltip" />
      

    </div>
  );
}

export default App;

